@import url('https://fonts.googleapis.com/css2?family=Paytone+One&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing:border-box;
}
/* font-family: 'Paytone One', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Playfair Display', serif; */

/* navbar css */
.navbar{
  font-family: 'Poppins', sans-serif;
  font-size:17px;
  /* line-height:26px; */
  text-transform:uppercase;
  box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important; 

}
.nav-link{
  font-weight: 300px !important;
}
.active{
   border-bottom: 2px solid black;
}
.navbar-brand{
  font-weight: 700px;
  font-family: 'roboto', 'sans-serif';
  letter-spacing:3px;
}

.footer{
  color:white;
  padding: 25px;  
  background: linear-gradient(to right,#434343,#000000);
}

.footer a{
  text-decoration: none;
  color:white;
  padding: 10px;

}

.footer a:hover{
  color:#ffefba;
  border-bottom:1px solid #ffefba;
}
/* Page Not Found CSS */
.pnf{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title{
  font-size:100px;
  font-weight:700;
}

.pmf-heading{
  font-weight:normal;
}

.pnf-btn{
  color:black;
  border:2px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}


/* Contact Us */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}


.product-link{
  text-decoration: none;
  color:black !important
